}

body {
  min-width: 320px;
  color: #000;
  background: #fff;
  font-family: "Montserrat", Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.3;
}

@media only screen and (max-width: 1025px) {
  body {
    padding-top: 70px;
  }
}

.container {
  position: relative;
  margin: 0 auto;
  width: 100%;
  max-width: 1440px;
}

.inner {
  position: relative;
  z-index: 100;
  padding: 0 20%;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1025px) {
  .inner {
    padding-left: 30px;
    padding-right: 30px;
  }
}

.fullscreen {
  position: relative;
  height: 100vh;
}

.fullscreen__section {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
}

@media only screen and (max-width: 1025px) {
  .fullscreen__section {
    position: relative;
    display: block;
    opacity: 0;
  }
}

.fullscreen__section.is-useless {
  pointer-events: none;
}

.fullscreen__section.is-active {
  z-index: 10;
}

.fullscreen__nav {
  position: absolute;
  z-index: 300;
  top: 50%;
  left: 45px;
  color: #2147cd;
  transform: translate3d(0, -50%, 0);
  transition: opacity 0.6s, color 0.4s;
}

@media only screen and (max-width: 1025px) {
  .fullscreen__nav {
    display: none;
  }
}

.fullscreen__nav.is-active {
  opacity: 1;
}

.fullscreen__nav[data-theme="white"] {
  color: #fff;
}

.fullscreen__nav[data-theme="white"] .fullscreen__nav-btn:before {
  background: #fff;
}

.fullscreen__nav[data-theme="white"] .fullscreen__counter-dots i {
  border-color: #fff;
  background: #e9e9e9;
}

.fullscreen__nav-btn {
  padding: 11px 0;
  background: none;
}

.fullscreen__nav-btn.is-active:before {
  transform: scaleX(1);
}

.fullscreen__nav-btn:before {
  content: '';
  display: block;
  width: 35px;
  height: 3px;
  background: #2147cd;
  transform: scaleX(0.5);
  transform-origin: left center;
  transition: transform 0.3s, background 0.3s;
}

.fullscreen__counter {
  position: absolute;
  top: 100%;
  left: 0;
  padding: 20px 0 0;
  white-space: nowrap;
}

.fullscreen__counter-main, .fullscreen__counter-dots, .fullscreen__counter-total {
  display: inline-block;
  vertical-align: bottom;
}

.fullscreen__counter-main {
  font-family: "Gotham Pro", Helvetica, sans-serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 1.25;
  letter-spacing: 0.005em;
}

.fullscreen__counter-dots {
  padding: 0 5px 24px;
  font-size: 0;
}

.fullscreen__counter-dots i {
  display: inline-block;
  vertical-align: top;
  border: 1px solid #2147cd;
  width: 6px;
  height: 6px;
  background: rgba(33, 71, 205, 0.8);
  transition: border-color 0.4s, background 0.4s;
}

.fullscreen__counter-dots i:not(:last-child) {
  margin-right: 5px;
}

.fullscreen__counter-total {
  padding: 0 0 18px 5px;
  font-family: "Gotham Pro", Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.25;
}

.fullscreen__arrows {
  padding: 2px 0 0;
  margin: -20px;
  text-align: right;
}

@media only screen and (max-width: 767px) {
  .fullscreen__arrows {
    display: none;
  }
}

.fullscreen__arr {
  padding: 20px;
  color: inherit;
  background: none;
  text-align: center;
}

.fullscreen__arr-icon {
  fill: currentColor;
  font-size: 20px;
}

[data-animation-from="fade"] {
  opacity: 0;
}

[data-animation-from="fade-top"] {
  opacity: 0;
  transform: translateY(-30px);
}

[data-animation-from="fade-bottom"] {
  opacity: 0;
  transform: translateY(30px);
}

.title-xl {
  font-family: "Gotham Pro", Helvetica, sans-serif;
  font-size: 48px;
  font-weight: 700;
