.btn-nav {
  position: absolute;
  z-index: 200;
  top: 43px;
  left: 25px;
  padding: 20px;
  background: none;
}

@media only screen and (max-width: 1025px) {
  .btn-nav {
    top: 10px;
    left: 10px;
  }
