.no-touch .btn-nav:hover span:last-child {
  transform: scaleX(1);
}

.btn-nav span {
  display: block;
  width: 17px;
  height: 3px;
  background: #2147cd;
  transition: transform 0.3s, background 0.3s;
}

@media only screen and (max-width: 1025px) {
