.title-xl {
	font-family: $fontGotham;
	@include f(48px, 700, 1.25);
	letter-spacing: 0.005em;
	@include md {
		@include f(36px, 900, 1.111); }
	&_black {
		font-family: $fontPrimary;
		font-weight: 900; } }

.title-lg {
	@include f(38px, 900, 1.11);
	letter-spacing: 0.005em; }

.title-md {
	@include f(36px, 900, 1.11);
	letter-spacing: 0.005em; }
