.box {
	&__inner {
		color: $blue; }
	&__figure {
		margin-bottom: 25px;
		width: 68px;
		height: 58px;
		@include vertical;
		@include xs {
			margin: 0 auto 10px;
			height: 56px; } }
	&__icon {
		display: inline-block;
		vertical-align: middle;
		max-width: 100%;
		max-height: 100%; }
	&__title {
		@include f(14px, 700, 1);
		text-decoration: underline;
		@include xs {
			@include f(15px, 700, 1.1333); } } }
