.sm-box {
	@include clr;
	@include xs {
		display: flex;
		flex-direction: row;
		align-items: center; }
	&__figure {
		float: left;
		width: 68px;
		height: 60px;
		@include vertical;
		@include xs {
			float: none;
			width: 93px;
			min-width: 93px;
			height: 77px; }
		img {
			display: inline-block;
			vertical-align: middle;
			max-width: 100%;
			max-height: 100%; } }
	&__content {
		margin-left: 118px;
		letter-spacing: 0.005em;
		@include xs {
			flex-grow: 1;
			padding-left: 50px;
			margin-left: 0; } }
	&__title {
		display: block;
		color: currentColor;
		@include f(14px, 600, 1);
		text-decoration: underline;
		@include xs {
			@include f(18px, 700, 1.111); } }
	&__description {
		padding-top: 10px;
		@include f(10px, 500, 1.5);
		@include xs {
			display: none; } } }
