//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$fontPrimary: 'Montserrat';
$fontGotham: 'Gotham Pro';
$fontMyriad: 'Myriad Pro';

//Gotham Pro bold
@font-face {
	@include fontdefault(normal, 700, $fontGotham);
	@include fontpath($fontGotham + ' Bold', $fontGotham + '-Bold', GothamPro-Bold); }

//Myriad Pro bold
@font-face {
	@include fontdefault(normal, 700, $fontMyriad);
	@include fontpath($fontMyriad + ' Bold', $fontMyriad + '-Bold', MyriadPro-Bold); }

//Primary
@font-face {
	@include fontdefault(normal, 400, $fontPrimary);
	@include fontpath($fontPrimary + ' Regular', $fontPrimary + '-Regular', montserrat-regular); }
@font-face {
	@include fontdefault(italic, 400, $fontPrimary);
	@include fontpath($fontPrimary + ' Regular Italic', $fontPrimary + '-Regular-Italic', montserrat-italic); }

@font-face {
	@include fontdefault(normal, 500, $fontPrimary);
	@include fontpath($fontPrimary + ' Medium', $fontPrimary + '-Medium', montserrat-medium); }
@font-face {
	@include fontdefault(italic, 500, $fontPrimary);
	@include fontpath($fontPrimary + ' Medium Italic', $fontPrimary + '-Medium-Italic', montserrat-mediumitalic); }

@font-face {
	@include fontdefault(normal, 600, $fontPrimary);
	@include fontpath($fontPrimary + ' Semibold', $fontPrimary + '-Semibold', montserrat-semibold); }
@font-face {
	@include fontdefault(italic, 600, $fontPrimary);
	@include fontpath($fontPrimary + ' Semibold Italic', $fontPrimary + '-Semibold-Italic', montserrat-semibolditalic); }

@font-face {
	@include fontdefault(normal, 700, $fontPrimary);
	@include fontpath($fontPrimary + ' Bold', $fontPrimary + '-Bold', montserrat-bold); }
@font-face {
	@include fontdefault(italic, 700, $fontPrimary);
	@include fontpath($fontPrimary + ' Bold Italic', $fontPrimary + '-Bold-Italic', montserrat-bolditalic); }

@font-face {
	@include fontdefault(normal, 900, $fontPrimary);
	@include fontpath($fontPrimary + ' Black', $fontPrimary + '-Black', montserrat-black); }
@font-face {
	@include fontdefault(italic, 900, $fontPrimary);
	@include fontpath($fontPrimary + ' Black Italic', $fontPrimary + '-Black-Italic', montserrat-blackitalic); }
