.list {
	max-width: 800px;
	@include supports(column-count, 3) {
		column-count: 3;
		@include sm {
			column-count: 2; }
		@include xs {
			column-count: 1; } }
	&__item {
		position: relative;
		padding: 8px 20px 8px 23px;
		@include f(18px, 400, 1);
		-webkit-column-break-inside: avoid;
		page-break-inside: avoid;
		break-inside: avoid-column;
		@include ie {
			display: inline-block;
			vertical-align: top;
			width: 33.33%;
			@include sm {
				width: 50%; }
			@include xs {
				width: 100%; } }
		&:before {
			content: '';
			position: absolute;
			top: 12px;
			left: 0;
			border: 3px solid $red;
			border-radius: 50%;
			width: 12px;
			height: 12px;
			background: $white; } } }

